import React from 'react'
import { Carousel, SearchBar } from 'antd-mobile';
import './Home.css'
import routes1 from './Routes'
import { Link } from 'react-router-dom';
import Mytab from './Mytab'

export default class App extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        listArr: []
      }
      let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'get'
        }).then(res=>res.json())
        .then((res)=>{
            this.setState({
                listArr: res.data
            })
        })
    }
    render() {
        return (
            <div>
                <div onClick={() => this.props.history.push('/search')}
                    style={{ zIndex: '1', width: '100%', height: '50px', backgroundColor: 'orange', position: 'fixed', top: '0px' }}>
                    <span style={{ float: 'left', color: 'white', fontSize: '30px' }}>淘</span>
                    <SearchBar placeholder='寻找宝贝店铺' style={{ marginLeft: '40px', width: '95%', height: '25px', marginTop: '10px', textAlign: 'center', paddingTop: '5px' }} />
                </div>
                <Carousel infinite autoplay autoplaySpeed={3000} style={{ marginTop: '50px' }}>
                    <img src="https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg" />
                    <img src="https://gw.alicdn.com/imgextra/i1/117/O1CN01bkQgWE1CjcyFpMxnm_!!117-0-lubanu.jpg" />
                    <img src="https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000000089/O1CN01TMVRjt1CWnsb14yoI_!!6000000000089-0-octopus.jpg" />
                    <img src="https://img.alicdn.com/imgextra/i3/200/O1CN015M1SHX1DLdqOXF3jO_!!200-0-luban.jpg" />
                    <img src="https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg" />
                </Carousel>
                <div style={{ width: '100%', backgroundColor: 'rgb(244,244,244)' }}>
                    {this.state.count}
                    <ul style={{ width: '90%', textAlign: 'center', listStyle: 'none',float: 'left',margin: '0 auto' ,padding: '0 5%'}}>
                        {
                            this.state.listArr.map(item=>{
                                return (
                                    <li style={{ marginBottom: '50px',float: 'left',width: '48%',margin: '1%' }} key={item.id}>
                                        <div onClick={() => this.props.history.push('/information?img=' + item.img + '&title=' + item.title + '&id=' + item.id + '&price=' + item.price)} style={{ width: '100%', backgroundColor: 'white', borderRadius: '3%', display: 'inline-block', marginRight: '10%' }}>
                                            <img style={{ width: '100%' }} src={item.img} />
                                                <p id="p1" style={{fontSize: '1.2rem'}}>{item.title}</p>
                                                <p id="p2" ><span id="sp" style={{fontSize: '1.2rem'}}>¥{ item.price }</span> <span style={{fontSize: '1.2rem'}} id="sp1">{item.id}人已买</span></p>
                                        </div>
                                    </li>
                                )
                            })
                        }
                        
                    </ul>
                </div>
                <Mytab />
            </div>
        )
    }
  }
