import React from 'react'
import { Carousel, SearchBar } from 'antd-mobile';
import Mytab from './Mytab';

const Search = (props) => {
    return (
        <div style={{ width: '100%', height: '50px', backgroundColor: 'orange', position: 'fixed', top: '0px' }}>
            <SearchBar style={{ marginLeft: '40px', width: '95%', height: '25px', marginTop: '10px', textAlign: 'center', paddingTop: '5px' }} onCancel={() => props.history.push('/')}/>
            <Mytab />
        </div>
    )
}

export default Search