import React from 'react';
import Tab from './Tab'
import './Home.css'
const queryString = require('query-string');

export default class Information extends React.Component {
    constructor(props) {
        super(props)
        const info = window.location.search.replace("?", "");
        const info_ = queryString.parse(info);
        let SData = localStorage.getItem('SData') ? JSON.parse(localStorage.getItem('SData')) : []
        let flag = true
        
        this.state = {
          title: info_.title,
          id: info_.id,
          img: info_.img,
          price: info_.price,
          flag
        }
        SData.forEach(item=>{
            if(item.id === this.state.id){
                flag = false
            }
        })
    }
    setDatas = ()=>{
        let SData = localStorage.getItem('SData') ? JSON.parse(localStorage.getItem('SData')) : []
        let flag = true
        let index = 0
        SData.forEach((item,i)=>{
            if(item.id === this.state.id){
                flag = false
                index = i
            }
        })
        if(flag){
            SData.push({
                id: this.state.id,
                title: this.state.title,
                img: this.state.img,
                price: this.state.price
            })
            localStorage.setItem('SData',JSON.stringify(SData))
            alert('收藏成功')
        }else{
            localStorage.setItem('SData',JSON.stringify(SData.splice(index,1)))
            alert('取消收藏成功')
        }
    }

    render() {
        return (
            <div>
                <div style={{ width: '80%', height: '350px', backgroundRepeat: 'no-repeat', backgroundImage: 'url(' + new URL(window.location).searchParams.get("img") + ')' }}>
                    
        <div style={{ width: '30px', height: '30px', borderRadius: '50%', backgroundColor: 'rgb(244,244,244,0.5)', lineHeight: '30px',textAlign:"center" }} onClick={() => this.props.history.push('/')}>{'<'}</div>
                </div>
                <p id="p1" style={{ fontSize: '1.2rem' }}>{this.state.title}</p>
                <p id="p2" ><span id="sp" style={{ fontSize: '1.2rem' }}>¥{this.state.price}</span> <span style={{ fontSize: '1.2rem' }} id="sp1">{this.state.id}人已买</span></p>
                <Tab MakeMoney={this.setDatas} flag={this.state.flag}/>
            </div>
        )
    }
}
