import React from 'react'

const Login = (props) => {
    const login = ()=>{
        localStorage.setItem('userinfo','tom')
        props.history.replace(props.location.state)
    }
    return (
        <div>
            <div style={{backgroundImage:"url(https://dss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1256913123,1038353873&fm=26&gp=0.jpg)",backgroundSize:"cover",width:"100px",height:"100px",margin:'70px auto',borderRadius:"50%"}}></div>
            <input type="text" placeholder="手机号/邮箱/会员名" style={{width:"100%",height:"80px",borderBottom:"2px solid orange",borderTop:"0px"}}></input>
            <br/>
            <input type="password" placeholder="请输入登录密码" style={{width:"100%",height:"80px",borderBottom:"2px solid orange",borderTop:"0px"}}></input>
            <button onClick={login} style={{width:"80%",height:"50px",backgroundColor:"orange",border:"1px solid orange",marginLeft:"10%",marginTop:"50px",borderRadius:"20px",color:"white"}}>登录</button>
        </div>
    )
}

export default Login
