import React from 'react'
import Mytab from './Mytab'
export default class List extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            SData: window.localStorage.getItem('SData') ? JSON.parse(window.localStorage.getItem('SData')) : []
        }

    }
    render() {
        return (
            <div>
                <div style={{ width: '100%', backgroundColor: 'rgb(244,244,244)' }}>
                    {this.state.count}
                    <ul style={{ width: '90%', textAlign: 'center', listStyle: 'none', float: 'left', margin: '0 auto', padding: '0 5%' }}>
                        {
                            this.state.SData.map(item => {
                                return (
                                    <li style={{ marginBottom: '50px', float: 'left', width: '48%', margin: '1%' }} key={item.id}>
                                        <div onClick={() => this.props.history.push('/information?img=' + item.img + '&title=' + item.title + '&id=' + item.id)} style={{ width: '100%', backgroundColor: 'white', borderRadius: '3%', display: 'inline-block', marginRight: '10%' }}>
                                            <img style={{ width: '100%' }} src={item.img} />
                                            <span style={{ color: 'red' }} onClick={(e) => {
                                                e.stopPropagation();
                                                this.state.SData.forEach((element, i) => {
                                                    if (element.id === item.id) {
                                                        this.setState({
                                                            SData: this.state.SData.splice(i, 1)
                                                        })
                                                        window.localStorage.setItem('SData', JSON.stringify(this.state.SData));
                                                        window.location.reload([true]) 
                                                        alert('删除成功')
                                                    }
                                                })
                                            }}>删除</span>
                                            <p id="p1" style={{ fontSize: '1.2rem' }}>{item.title}</p>
                                            <p id="p2" ><span id="sp" style={{ fontSize: '1.2rem' }}>¥{item.price}</span> <span style={{ fontSize: '1.2rem' }} id="sp1">{item.id}人已买</span></p>
                                        </div>

                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
                <Mytab />
            </div>
        )
    }
}

