import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        if (pathname.split('/').length > 2) {
            return null;
        }
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <div style={{ width: '60%', float: 'left' }}>
                    <TabBar
                        unselectedTintColor="#949494"
                        tintColor="red"
                        barTintColor="white"
                    >
                        <TabBar.Item
                            title="店铺"
                            key="home"
                            icon={
                                <i className='iconfont icon-dianpu'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-dianpu'></i>
                            }
                            selected={this.state.selectedTab === 'greenTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'greenTab',
                                });
                            }}
                            
                            data-seed="logId"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-kefuwangwang'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-kefuwangwang'></i>
                            }
                            title="客服"
                            key="cart"
                            selected={this.state.selectedTab === 'redTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'redTab',
                                });
                            }}
                            data-seed="logId1"
                        ></TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-shoucang'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-shoucang1'></i>
                            }
                            title="收藏"
                            key="Friend"
                            selected={this.state.selectedTab === 'yellowTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'yellowTab'
                                })
                                this.props.MakeMoney()
                            }}
                        ></TabBar.Item>
                    </TabBar>
                </div>
                <div style={{ backgroundColor: 'white' }}>
                    <button style={{ width: '15%', marginTop: '7px', height: '35px', backgroundColor: 'orange', borderRadius: '15px 0 0 15px', borderColor: 'orange', color: 'white' }}>加入购物车</button>
                    <button style={{ width: '15%', marginTop: '7px', height: '35px', backgroundColor: 'red', borderRadius: '0 15px 15px 0', borderColor: 'red', color: 'white' }}>立即购买</button>
                </div>
            </div>
        );
    }
}
export default withRouter(Mytab);