import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Button,TabBar} from 'antd-mobile'
import Mytab from './Mytab';
import Home from './Home';
import List from './Cart';
import Order from './Order';
import My from './My';
import Information from './Informatica';
import Search from './Search';
import Login from './Login'

const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} /> 
			<Route exact path='/login' component={Login} />  
			<PrivateRoute path='/cart' component={List} />
			<Route path='/order' component={Order} />
			<Route path='/my' component={My} />
			<Route path='/information' component={Information}/>
			<Route path='/search' component={Search}/>
			<Route path='/home/detail' component={List} />
			<Route render={()=><Redirect to='/'/>} />
		</Switch>
	</Router>
}


export default App;

const PrivateRoute = ({ component: Com, ...rest }) => {
	return (
		<Route
			{...rest}
			render={({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest} />
					: (
						<Redirect
							to={{
								pathname: "/login",
								state: location
							}}
						/>
					)
			}
		/>
	);
}
